.offers {
  color: $text-color;
  h2 {
    font-family: "Lato-ExtraBold", sans-serif;
    font-size: 32px;
    line-height: 1.2;
    margin: 0 0 40px;
  }
  .offers-blocks {
    display: grid;
    gap: 40px;
    @media (min-width: 961px) {
      grid-template-columns: 1fr 1fr;
      &-item:first-child, &-item:last-child {
        grid-column-start: 1;
        grid-column-end: 3;

        h3 {
          font-size: 74px;
          line-height: 1;
        }

        p {
          font-size: 26px;
          line-height: 1.2;
        }
      }
    }
    &-item {
      position: relative;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 350px;
      border-radius: 10px;
      display: flex;
      align-items: flex-end;
      & .backgroundgradient {
        position: absolute;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%);
        width: 100%;
        height: 100%;
        z-index: 0;
        left: 0;
        top: 0;
      }
      &-text {
        z-index: 1;
        display: flex;
        flex-direction: column;
        padding: 37px 30px;
        gap: 22px;
        position: relative;
      }
      h3 {
        font-family: "Lato-ExtraBold", sans-serif;
        font-size: 46px;
        line-height: 1;
        margin: 0;
      }
      p {
        font-family: "Lato-ExtraBold", sans-serif;
        font-size: 24px;
        line-height: 1.2;
        margin: 0;
      }
    }
  }
  @media (max-width: 960px) {
    h2 {
      font-size: 24px;
      margin-bottom: 30px;
    }
    .offers-blocks {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
      &-item {
        min-height: 339px;
        flex-basis: calc(50% - 15px);
        h3 {
          font-size: 40px;
        }
        p {
          font-size: 16px;
        }
      }
    }
  }
  @media (max-width: 720px) {
    h2 {
      font-size: 18px;
    }
    .offers-blocks {
      gap: 30px;
      &-item {
        flex-basis: 100%;
      }
    }
  }
}
