.about {
  position: relative;
  padding: 171px 0 104px 81.5px;
  text-align: left;
  color: $text-color;

  img {
    position: absolute;
    top: 50%;
    width: 327.13px;
    right: 90px;
    transform: translateY(-50%);
    z-index: 0;
  }
  .about-text {
    display: flex;
    flex-direction: column;
    padding: 0px;
    gap: 22px;
    position: relative;
    z-index: 1;
  }

  h1 {
    font-family: "Lato-ExtraBold", sans-serif;
    font-size: 72px;
    line-height: 1;
    margin: 0;
    span {
      color: $primary-color;
    }
  }
  h4 {
    font-family: "Lato-ExtraBold", sans-serif;
    font-size: 32px;
    line-height: 1.2;
    margin: 0;
    span {
      color: $button-color;
    }
  }

  @media(max-width: 960px) {
    padding: 180px 0 100px 30px;
    img {
      width: 299.5px;
      right: -73px;
    }
    h1 {
      font-size: 48px;
    }
    h4 {
      font-size: 22px;
    }
  }
  @media(max-width: 520px) {
    margin-top: -68px;
    padding:  0 16px 112px 16px;
    display: flex;
    flex-direction: column-reverse;
    gap: 30px;
    img {
      position: relative;
      right: 0;
      top: 0;
      transform: translateY(0px);
    }
    .about-text {
      align-items: center;
      width: 100%;
    }
    h1 {
      font-size: 36px;
    }
    h4 {
      font-size: 16px;
    }
  }
}