/* HEADER */
header {
  position: fixed;
  height: 80px;
  left: 0px;
  top: 0px;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  background: $menu-background;
  padding: 22.5px 82px;
  box-sizing: border-box;
  z-index: 11;

  @media(max-width: 960px) {
    padding: 22.5px 50px 22.5px 30px;
  }
  @media(max-width: 520px) {
    height: 48px;
    padding: 12px 20px;
  }
  @media(min-width: 521px) {
    .header-nav-burgerCheckbox, .header-nav-burger {
      display: none;
    }
  }

  .header-logo {
    height: -webkit-fill-available;
  }

  .header-nav {

    ul {
      list-style-type: none;
      display: flex;
      gap: 56px;
      padding: 0;
      margin: 0;

      li {
        font-family: 'Lato-Bold', sans-serif;
        font-size: 18px;
        line-height: 21.6px;

        a {
          background: #ffffff;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          text-fill-color: transparent;
          text-decoration: none;

          &:hover {
            background: linear-gradient(98.25deg, $button-color 18.56%, $primary-color 76.02%);
            -webkit-background-clip: text;
          }
        }
      }
    }

    @media (max-width: 520px) {

      .header-nav-list {
        top: 0;
        left: 0;
        position: absolute;
        flex-direction: column;
        gap: 12px;
        padding: 20px 0;
        list-style-type: none;
        transform: translateX(-100%);
        transition: .3s;
        width: 100%;
        background: black;
        margin-top: 48px;

        li {
          display: block;
          text-align: center;
          text-decoration: none;
        }
      }
      .header-nav-burgerCheckbox:checked ~ .header-nav-list {
        transform: translateX(0);
      }
    }
  }
}

.header-nav-burgerCheckbox {
  position: absolute;
  visibility: hidden;
}
.header-nav-burger {
  cursor: pointer;
  display: block;
  position: relative;
  border: none;
  background: transparent;
  width: 22.5px;
  height: 17.5px;
  margin: 30px auto;

  &::before, &::after {
    content: '';
    left: 0;
    position: absolute;
    display: block;
    width: 100%;
    height: 1.5px;
    background: $text-color;
  }

  &::before {
    top: 0;
    box-shadow: 0 8px 0 $text-color;
    transition: box-shadow .3s .15s, top .3s .15s, transform .3s;
  }

  &::after {
    bottom: 0;
    transition: bottom .3s .15s, transform .3s;
  }
}

.header-nav-burgerCheckbox:checked + .header-nav-burger {
  &::before {
    top: 8px;
    transform: rotate(45deg);
    box-shadow: 0 8px 0 rgba(0, 0, 0, 0);
    transition: box-shadow .15s, top .3s, transform .3s .15s;
  }

  &::after {
    bottom: 8px;
    transform: rotate(-45deg);
    transition: bottom .3s, transform .3s .15s;
  }
}
.link-svg:hover {
  #segment {
    fill-opacity: 0.6;
  }

  /* gradient */
  #fade-stop-1 {
    stop-color: $button-color;
  }

  #fade-stop-2 {
    stop-color: $primary-color;
  }
}