.btn {
  padding: 14px 40px 14px 40px;
  border: 0;
  border-radius: 100px;
  font-size: 22px;
  line-height: 1.2;
  width: max-content;
  cursor: pointer;
  &-primary {
    background: $button-color;
    &:hover {
      background: rgba($button-color, 0.6);
    }
  }
  &-secondary {
    border: 2px solid $button-color;
    color: $button-color;
    background: transparent;
    &:hover {
      border-color: rgba($button-color, 0.6);
      color: rgba($button-color, 0.6);
    }
    @media (max-width: 960px) {
      padding: 7px 40px 7px 40px;
      font-size: 16px;
      line-height: 1.2;
    }
  }
  @media (max-width: 520px) {
    padding: 7px 40px 7px 40px;
    font-size: 16px;
    line-height: 1.2;
  }
}