.info-text {
  color: #fff;
  & > *:not(:last-child) {
    padding: 10px 0;
    margin: 0px;
  }
  h4 {
    font-family: "Lato-ExtraBold", sans-serif;
    font-size: 32px;
    line-height: 38.4px;
  }
  p, .link {
    font-size: 20px;
    line-height: 24px;
  }
  .link {
    font-family: "Lato-ExtraBold", sans-serif;
    cursor: pointer;
    &-primary {
      color: $button-color;
      &:hover {
        color: rgba($button-color, 0.6);
      }
    }
  }
  @media (max-width: 960px) {
    h4 {
      font-size: 24px;
      line-height: 28.8px;
    }
    p, .link {
      font-size: 16px;
      line-height: 19.2px;
    }
  }
  @media (max-width: 520px) {
    h4 {
      font-size: 18px;
      line-height: 21.6px;
    }
    p, .link {
      font-size: 14px;
      line-height: 16.8px;
    }
  }
}