.info {
  padding: 0 81.5px 62px;
  background: $base-color;
  & > * {
    padding-top: 62px;
  }
  @media (max-width: 960px) {
    padding: 0 29px 53px;
    & > * {
      padding-top: 53px;
    }
  }
  @media (max-width: 520px) {
    padding: 0 20px 50px;
    & > * {
      padding-top: 50px;
    }
  }
}